.c-header {
	height: 100vh;
	position: relative;
	background-color: $orange;
	display: flex;
	justify-content: space-between;
	flex-direction: column;
	padding-top: 80px;
	padding-bottom: 100px;
	@include respond-to(medium-screens) {
		padding-top: 60px;
		padding-bottom: 80px;
	}
	@include respond-to(tablets) {
		padding-top: 40px;
		padding-bottom: 40px;
	}
	@include respond-to(handhelds) {
		padding-top: 30px;
		padding-bottom: 40px;
	}
	&__header {
		&-menu {
			text-align: right;
			margin-right: 120px;
			@include respond-to(tablets) {
				margin-right: 40px;
			}
			@include respond-to(handhelds) {
				margin-right: 20px;
			}
			&-item {
				margin: 20px;
				text-decoration: none;
				letter-spacing: $letter-spacing;
				color: $white;
				font-size: $medium-font;
				@include respond-to(handhelds) {
					margin: 10px;
				}
				&:hover {
					text-decoration: underline;
				}
			}
		}
	}
	&__logo {
		text-align: center;
	}
	&__main {
		&-title {
			text-align: center;
			color: $white;
			font-size: 36px;
			letter-spacing: $letter-spacing;
			margin-bottom: 40px;
			padding: 0 180px;
			line-height: 1.4;
			@include respond-to(tablets) {
				font-size: 28px;
			}
			@include respond-to(handhelds) {
				font-size: 18px;
				padding: 0 50px;
			}
		}
	}
}

.c-button{
	&.c-header__header-menu-item {
		position: absolute;
		background-color: transparent;
		margin: 0;
		top: 70px;
		left: 120px;
		@include respond-to(medium-screens) {
			top: 50px;
		}
		@include respond-to(tablets) {
			top: 30px;
			left: 40px;
		}
		@include respond-to(handhelds) {
			top: 15px;
			left: 15px;
		}
		&:hover {
			background-color: rgba(0, 0, 0, .5);
		}
	}
	&.c-header__button {
		background-color: $green;
		&:hover {
			background-color: $dark-green;
		}
	}
}

.button-wrapper {
	display: block;
	margin: 0 auto;
	width: 140px;
}
